___CSS_LOADER_EXPORT___.push([module.id, `* {
    margin: 0;
    padding: 0;
    text-decoration: none;
    list-style: none;
    color: white;
    box-sizing: border-box;
}

textarea, select, input, button { outline: none; }

:root {
    font-family: 'Courier New', Courier, monospace;
    font-size: 18px;
}

body {
    height: 100vh;
    width: 100vw;
    display: flex;
    align-items: stretch;
    background-color: rgb(26, 26, 31);
}

.side-bar {
    background-color: rgb(0, 12, 53);
    display: flex;
    flex-direction: column;
    gap: 32px;
}

.side-bar-projects > li, .side-bar-dates > li:not(#calender), .side-bar .home {
    cursor: pointer;
}

#calender {
    color: rgb(111, 111, 111);
}

.side-bar > .home , .side-bar-projects > li, .side-bar-dates > li {
    padding-left: 16px;
    padding: 4px 64px 4px 32px;
}

#user-info {
    background-color: rgb(0, 18, 77);
    padding: 8px;
    display: flex;
    align-items: center;
}

#user-info img {
    height: 64px;
}

.content-container {
    padding-left: 25%;
    padding-right: 25%;
}

body > :last-child {
    flex: 1;
    text-align: center;
}

header {
    font-size: 2rem;
    padding-top: 32px;
    padding-bottom: 16px;
}

.form-container {
    min-width: 300px;
    padding: 0 15%;
    height: 200px;
    background-color: rgb(75, 75, 75);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 8px;
}
.todo-input {
    position: relative;
    align-self: stretch;
}

.todo-input img {
    position: absolute;
    height: 48px;
    right: 0;
    cursor: pointer;
}

#todo-text {
    width: 100%;
    height: 48px;
    padding: 2px;
    font-size: 1.5rem;
}
.form-container > .second-row {
    align-self: stretch;
    display: flex;
    justify-content: space-between;
}
.form-container * {
    color: black;
}

.project-selector-holder {
    display: flex;
    gap: 4px;
    flex-direction: column;
}

#new-group.false {
    border: 1px solid red;
}

.project-selector {
    display: flex;
    gap: 4px;
}

.add-button {
    height: 100%;
    width: 32px;
    font-size: 1rem;
}

.form-container > .second-row select, .form-container > .second-row input{
    width: 120px;
    height: 24px;
}
.todo-column {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    gap: 6px;
}
.todo-item {
    border-top: 2px solid black;
    padding: 16px 0;
    padding: 16px 15%;
    display: flex;
    text-align: start;
    align-items: center;
}

.todo-container {
    background-color: rgb(43, 40, 36);
    border-bottom: 2px solid rgb(0, 0, 0);
}

.todo-column > .first-row {
    display: flex;
    justify-content: space-around;
}

.second-column button {
    color: rgb(205, 53, 53);
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,SAAS;IACT,UAAU;IACV,qBAAqB;IACrB,gBAAgB;IAChB,YAAY;IACZ,sBAAsB;AAC1B;;AAEA,kCAAkC,aAAa,EAAE;;AAEjD;IACI,8CAA8C;IAC9C,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,YAAY;IACZ,aAAa;IACb,oBAAoB;IACpB,iCAAiC;AACrC;;AAEA;IACI,gCAAgC;IAChC,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,kBAAkB;IAClB,0BAA0B;AAC9B;;AAEA;IACI,gCAAgC;IAChC,YAAY;IACZ,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,iBAAiB;IACjB,kBAAkB;AACtB;;AAEA;IACI,OAAO;IACP,kBAAkB;AACtB;;AAEA;IACI,eAAe;IACf,iBAAiB;IACjB,oBAAoB;AACxB;;AAEA;IACI,gBAAgB;IAChB,cAAc;IACd,aAAa;IACb,iCAAiC;IACjC,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,sBAAsB;IACtB,QAAQ;AACZ;AACA;IACI,kBAAkB;IAClB,mBAAmB;AACvB;;AAEA;IACI,kBAAkB;IAClB,YAAY;IACZ,QAAQ;IACR,eAAe;AACnB;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,YAAY;IACZ,iBAAiB;AACrB;AACA;IACI,mBAAmB;IACnB,aAAa;IACb,8BAA8B;AAClC;AACA;IACI,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,QAAQ;IACR,sBAAsB;AAC1B;;AAEA;IACI,qBAAqB;AACzB;;AAEA;IACI,aAAa;IACb,QAAQ;AACZ;;AAEA;IACI,YAAY;IACZ,WAAW;IACX,eAAe;AACnB;;AAEA;IACI,YAAY;IACZ,YAAY;AAChB;AACA;IACI,aAAa;IACb,sBAAsB;IACtB,YAAY;IACZ,QAAQ;AACZ;AACA;IACI,2BAA2B;IAC3B,eAAe;IACf,iBAAiB;IACjB,aAAa;IACb,iBAAiB;IACjB,mBAAmB;AACvB;;AAEA;IACI,iCAAiC;IACjC,qCAAqC;AACzC;;AAEA;IACI,aAAa;IACb,6BAA6B;AACjC;;AAEA;IACI,uBAAuB;AAC3B","sourcesContent":["* {\n    margin: 0;\n    padding: 0;\n    text-decoration: none;\n    list-style: none;\n    color: white;\n    box-sizing: border-box;\n}\n\ntextarea, select, input, button { outline: none; }\n\n:root {\n    font-family: 'Courier New', Courier, monospace;\n    font-size: 18px;\n}\n\nbody {\n    height: 100vh;\n    width: 100vw;\n    display: flex;\n    align-items: stretch;\n    background-color: rgb(26, 26, 31);\n}\n\n.side-bar {\n    background-color: rgb(0, 12, 53);\n    display: flex;\n    flex-direction: column;\n    gap: 32px;\n}\n\n.side-bar-projects > li, .side-bar-dates > li:not(#calender), .side-bar .home 